.item {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  background-color: salmon;
  border-radius: 1rem;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.2);
  height: 100%;
}
.posterBox {
  border: 1rem solid salmon;
  border-radius: 1rem;
  width: 100%;
}
.textBox {
  padding: 0 1rem 1rem;
  width: 100%;
}
.poster {
  border-radius: 1rem;
  width: 100%;
}
.title {
  font-size: 1.2rem;
  font-weight: bolder;
  letter-spacing: -0.5px;
  margin-bottom: 0.5rem;
}
.year {
  margin: 0.5rem;
}
.desc {
  margin: 1rem 0;
}
.tagGroup {
  display: flex;
  gap: 0.35rem;
  margin-bottom: 0.5rem;
  flex-wrap: wrap;
}
.tag {
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  color: wheat;
  background-color: steelblue;
  padding: 0.25rem 0.5rem;
  border-radius: 5px;
}
