.main {
  background-size: cover;
  padding: 2rem 0;
}
.container {
  padding: 4rem 2rem;
  background-color: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(2px);
  border-radius: 1rem;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.2);
  max-width: 80rem;
  width: 95%;
}
@media (max-width: 40rem) {
  .container {
    padding: 1rem 0.5rem;
    width: 95%;
  }
}

.detail {
  display: flex;
  align-items: flex-start;
  gap: 2rem;
}
@media (max-width: 40rem) {
  .detail {
    flex-direction: column;
  }
}
.youtube {
  border: 1rem solid rgba(255, 255, 255, 0.2);
  border-radius: 1rem;
  aspect-ratio: 12 / 7;
}
@media (max-width: 40rem) {
  .youtube {
    width: 100%;
    border-width: 0.5rem;
  }
}
.poster {
  border-radius: 1rem;
  height: auto;
  min-height: auto;
  aspect-ratio: 500 / 750;
  object-fit: contain;
}
@media (max-width: 40rem) {
  .poster {
    width: 100%;
  }
}
.title {
  font-size: 3rem;
  font-weight: bold;
  letter-spacing: -1.5px;
  margin: 1rem 0 1.5rem;
  line-height: 0.95;
}
@media (max-width: 40rem) {
  .title {
    font-size: 2rem;
  }
}
.desc {
  line-height: 1.3;
  font-size: 1.3rem;
  letter-spacing: 0.5px;
  margin: 2.5rem 0;
  font-weight: lighter;
  line-height: 1.5;
}
.tagGroup {
  display: flex;
  gap: 0.5rem;
  margin: 1.5rem 0;
  flex-wrap: wrap;
}
.tag {
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  color: wheat;
  background-color: steelblue;
  padding: 0.25rem 0.5rem;
  border-radius: 5px;
}
.etc {
  margin-bottom: 0.5rem;
  display: flex;
  gap: 0.5rem;
  align-items: center;
  color: rgba(255, 255, 255, 0.7);
}
.imdb_logo {
  font: 1em/1 Impact, HelveticaNeue-CondensedBold, sans-serif;
  color: #000;
  text-shadow: 0 0 1px #000, 0 0 0.15em #fff;
  text-decoration: none;

  display: inline-block;
  vertical-align: bottom;
  padding: 0.25em 0.35em;
  border-radius: 0.15em;
  background: radial-gradient(#ffffb8, #ce981d);

  &:hover {
    background: radial-gradient(lighten(#ffffb8, 10%), lighten(#ce981d, 10%));
  }
}
