.heading {
  font-size: 5rem;
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
  font-weight: bolder;
  margin: 2rem;
  padding: 1rem;
  color: wheat;
  background-color: steelblue;
  display: inline-flex;
  border-radius: 1rem;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
}

@media (max-width: 40rem) {
  .heading {
    font-size: 3.5rem;
    padding: 0.5rem;
    margin: 1rem 0.5rem;
  }
}
